* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

.App {
  min-width: 100vw;
}

.logo-nav {
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e6e6e6;
}

.logo-nav svg {
  font-size: 2rem;
}

.grid {
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 56px;
  padding-bottom: 56px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-gap: 2rem;
}

.cuisine-card img {
  width: 100%;
  border-radius: 2rem;
  transition: 0.3s;
}

.cuisine-card img:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.cuisine-card a {
  text-decoration: none;
}

.cuisine-card h4 {
  text-align: center;
  padding: 1rem;
}

.list {
  background-color: #e6e6e6;
  padding-left: 120px;
  padding-right: 120px;
  display: flex;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.sLink {
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 2rem;
  text-decoration: none;
  background: linear-gradient(35deg, #494949, #313131);
  width: 6rem;
  height: 6rem;
  cursor: pointer;
  transform: scale(0.8);
}
.sLink:hover,
.sLink:active {
  background: linear-gradient(to right, #f27121, #e94057);
}

.sLink h4 {
  color: white;
  font-size: 0.8rem;
}

.sLink svg {
  color: white;
  font-size: 1.8rem;
}

.wrapper {
  background-color: #f4f4f4;
  margin: 0rem 0rem;
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 56px;
  padding-bottom: 56px;
}

.veggie {
  background-color: #fff;
}

.wrapper h3 {
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: bold;
  line-height: 32px;
  text-align: center;
  color: black;
}

.wrapper .card {
  min-height: 205px !important;
  border-radius: 2rem;
  overflow: hidden;
  position: relative;
  transition: 0.5s;
  cursor: pointer;
}

.wrapper .card:hover {
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}

.wrapper .card p {
  position: absolute;
  z-index: 10;
  left: 50%;
  transform: translate(-50%, 120%);
  color: white;
  width: 100%;
  text-align: center;
  font-size: 1rem;
  height: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper .card img {
  border-radius: 2rem;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
}

.gradient {
  z-index: 3;
  border-radius: 2rem;
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
}

.search-container {
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 30px;
  padding-bottom: 30px;
}

.form-style {
  margin: 0rem 19.5rem;
  position: relative;
  width: 70%;
}

.form-style > input {
  border: none;
  background: linear-gradient(35deg, #494949, #313131);
  font-size: 1.5rem;
  color: white;
  padding: 1rem 3rem;
  border: none;
  border-radius: 1rem;
  outline: none;
  width: 70%;
}

.form-style svg {
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(100%, -50%);
  color: white;
}

.detail-wrapper {
  padding-left: 120px;
  padding-right: 120px;
  padding-top: 56px;
  padding-bottom: 56px;
  display: flex;
  max-width: 100vw;
}

.detail-wrapper h2 {
  margin-bottom: 2rem;
}

.detail-wrapper li {
  font-size: 1.2rem;
  line-height: 2.5rem;
}

.detail-wrapper ul {
  margin-top: 2rem;
}

.btn-container {
  margin-bottom: 32px;
}

.btn-container .button {
  padding: 1rem 2rem;
  color: #313131;
  background: white;
  border: 2px solid black;
  border-radius: 10px;
  margin-right: 2rem;
  font-weight: 600;
  display: inline-block;
  transition: background 0.2s ease-in-out;
  cursor: pointer;
}

.btn-container .button:hover {
  background: linear-gradient(35deg, #494949, #313131);
  color: white;
}

.btn-container .active-btn {
  background: linear-gradient(to right, #f27121, #e94057);
  color: white;
}

.recipe-summary {
  margin-bottom: 32px;
  font-weight: 500 !important;
}

.recipe-summary ol,
.recipe-ingredients {
  padding-left: 20px;
  font-weight: 500 !important;
}

.info {
  margin-left: 56px;
  font-weight: 500 !important;
}

